// components/Loading.jsx
import React from "react";
import "./LoadingPage.css"; // فرض بر این است که فایل CSS مناسب نیز ایجاد شده است

const LoadingPage = () => {
  return (
    <div className=" loading-screen">
      <div className="d-flex flex-column align-items-end">
        <div className="col-12 d-flex justify-content-center align-items-center flex-wrap">
          <div className="loading-spinner col-12"></div>
          <p className="loading_p col-12 text-center">
            Loading, please wait...
          </p>
        </div>
        <div className="col-12 mt-5"></div>
        <div className="col-12 mt-auto software-message text-center">
          <h6 className="welcome-to-nexgen mt-1">Welcome to NexGen1.1</h6>
        </div>
      </div>
    </div>
  );
};

export default LoadingPage;
