// app.jsx
import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Header from "./components/skeleton/Header";
import Footer from "./components/skeleton/Footer";
import Home from "./components/pages/menu/Home";
import XGPlan from "./components/pages/menu/XGPlan";
import Pool from "./components/pages/menu/Pool";
import Faq from "./components/pages/menu/Faq";
import Roadmap from "./components/pages/menu/Roadmap";
import StageNovice from "./components/pages/xgplan/stages/StageNovice";
import StageInitiate from "./components/pages/xgplan/stages/StageInitiate";
import StageApprentice from "./components/pages/xgplan/stages/StageApprentice";
import StageAdept from "./components/pages/xgplan/stages/StageAdept";
import StageExpert from "./components/pages/xgplan/stages/StageExpert";
import StageMaster from "./components/pages/xgplan/stages/StageMaster";
import StageGrandmaster from "./components/pages/xgplan/stages/StageGrandmaster";
import StageFree from "./components/pages/xgplan/stages/StageFree";
import UserProfile from "./components/pages/menu/UserProfile";
import TransactionsList from "./components/pages/menu/TransactionsList";
import MessagesList from "./components/pages/menu/MessagesList";
import StickyMenu from "./components/skeleton/StickyMenu";
import LoadingPage from "./components/loading/LoadingPage"; // اضافه شده
import XGPlanInfo from "./components/pages/xgplan/info/XGPlanInfo";

function App() {
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // تنظیم زمان نمایش صفحه لودینگ به مدت 7 ثانیه بعد از لود کامل اپلیکیشن
    const timer = setTimeout(() => {
      setLoading(false);
    }, 20000);

    // پاک کردن تایمر زمانی که کامپوننت unmount می‌شود
    return () => clearTimeout(timer);
  }, []);

  return (
    <div className="App">
      {loading && <LoadingPage />}
      {!loading && (
        <>
          <Header />
          <StickyMenu />
          <main id="main-content">
            <Routes>
              <Route path="/my-twa/stages/novice" element={<StageNovice />} />
              <Route
                path="/my-twa/stages/initiate"
                element={<StageInitiate />}
              />
              <Route
                path="/my-twa/stages/apprentice"
                element={<StageApprentice />}
              />
              <Route path="/my-twa/stages/adept" element={<StageAdept />} />
              <Route path="/my-twa/stages/expert" element={<StageExpert />} />
              <Route path="/my-twa/stages/master" element={<StageMaster />} />
              <Route
                path="/my-twa/stages/grandmaster"
                element={<StageGrandmaster />}
              />
              <Route path="/my-twa/stages/free" element={<StageFree />} />
              <Route path="/my-twa/faq" element={<Faq />} />
              <Route path="/my-twa/roadmap" element={<Roadmap />} />
              <Route path="/my-twa/xg-plan" element={<XGPlan />} />
              <Route path="/my-twa/xg-plan-info" element={<XGPlanInfo />} />
              <Route path="/my-twa/pool" element={<Pool />} />
              <Route path="/my-twa/profile" element={<UserProfile />} />
              <Route
                path="/my-twa/transactions/list"
                element={<TransactionsList />}
              />
              <Route path="/my-twa/messages/list" element={<MessagesList />} />
              <Route path="/my-twa/" element={<Home />} />
            </Routes>
          </main>
          <Footer />
        </>
      )}
    </div>
  );
}

export default App;
