// DashboardBalance.jsx
import React, { useState, useEffect } from "react";
import Skeleton, { SkeletonTheme } from "react-loading-skeleton";
import "react-loading-skeleton/dist/skeleton.css";
import { FaSyncAlt } from "react-icons/fa";
import axios from "axios";
import "./DashboardBalance.css"; // فرض بر این است که فایل CSS مناسب ایجاد شده است

const DashboardBalance = () => {
  const [balance, setBalance] = useState(null); // مقدار اولیه null است تا نشان دهنده حالت بارگذاری باشد
  const [isLoading, setIsLoading] = useState(true); // مقدار اولیه بارگذاری true است
  const [error, setError] = useState(false); // وضعیت خطا

  // تابع برای دریافت مقدار اولیه بالانس از سرور
  useEffect(() => {
    const fetchInitialBalance = async () => {
      try {
        const response = await axios.get("YOUR_API_ENDPOINT"); // ارسال درخواست به API
        setBalance(response.data.balance); // بروزرسانی balance با مقدار دریافت شده از سرور
      } catch (error) {
        console.error("Error fetching initial balance:", error); // چاپ خطا در صورت بروز مشکل
        setError(true); // تنظیم وضعیت خطا به true
      } finally {
        setIsLoading(false); // بازگشت وضعیت بارگذاری به false
      }
    };

    fetchInitialBalance();
  }, []);

  // تابع برای بروزرسانی balance با درخواست به سرور
  const refreshBalance = async () => {
    setIsLoading(true); // تنظیم وضعیت بارگذاری به true
    setError(false); // ریست وضعیت خطا
    try {
      const response = await axios.get("YOUR_API_ENDPOINT"); // ارسال درخواست به API
      setBalance(response.data.balance); // بروزرسانی balance با مقدار دریافت شده از سرور
    } catch (error) {
      console.error("Error fetching balance:", error); // چاپ خطا در صورت بروز مشکل
      setError(true); // تنظیم وضعیت خطا به true
    } finally {
      // تنظیم تایمر برای نگه داشتن وضعیت بارگذاری به مدت 3.5 ثانیه پس از دریافت پاسخ
      setTimeout(() => {
        setIsLoading(false); // بازگشت وضعیت بارگذاری به false
      }, 3500);
    }
  };

  return (
    <div className="col-12 dashboard-balance d-flex justify-content-center">
      <div className="dashboard-balance-frame d-flex justify-content-center align-items-center flex-wrap">
        <p className="col-12 text-center mb-3 d-flex justify-content-center align-items-center">
          <SkeletonTheme color="#ddd" highlightColor="#ccc">
            {isLoading ? (
              <Skeleton width={100} />
            ) : (
              <>
                <strong>{error ? "0xFaild" : balance}</strong>
                <small>USDT</small>
              </>
            )}
          </SkeletonTheme>
          <button
            className="refresh-button"
            onClick={refreshBalance}
            disabled={isLoading}>
            {isLoading ? <FaSyncAlt className="spinner" /> : <FaSyncAlt />}
          </button>
        </p>
        <div className="col-12 text-center activate-plans d-flex align-items-center justify-content-center">
          xG Plan Stage : FREE
          <br />
          ArBot : is Not Active for this stage
        </div>
      </div>
    </div>
  );
};

export default DashboardBalance;
