import React from "react";
import { Link } from "react-router-dom";
import "./Home.css";
import DashboardLottery from "../../dashboard/DashboardLottery";
import DashboardBalance from "../../dashboard/DashboardBalance";
import DashboardArbot from "../../dashboard/DashboardArbot";
function Home() {
  return (
    <div className="contentStyle">
      <div className="container-fluid">
        <div className="row">
          <div className="col-12 dashboard-allert-box">
            <div
              class="alert alert-secondary alert-dismissible fade show"
              role="alert">
              <small>
                Everything you might need to know about the rules, policies, and
                procedures of the NexGen platform.
                <Link to="/my-twa/Faq">FAQ</Link>
              </small>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="alert"
                aria-label="Close"></button>
            </div>
          </div>
          <DashboardBalance />
          <DashboardArbot />
          <DashboardLottery />
        </div>
      </div>
    </div>
  );
}

export default Home;
