import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Skeleton, { SkeletonTheme } from "react-loading-skeleton";
import "react-loading-skeleton/dist/skeleton.css";
import { FaSyncAlt } from "react-icons/fa";
import axios from "axios";
import Countdown from "react-countdown";
import icoArrowUp from "../../pbc/icon/select.svg";
import stkArBot from "../../pbc/icon/arbot512.svg";
import "./DashboardArbot.css"; // فرض بر این است که فایل CSS مناسب ایجاد شده است

const Completionist = () => <span>Time's up!</span>;

const DashboardArbot = () => {
  const calculateTimeRemaining = () => {
    const now = new Date();
    const targetTime = new Date();
    targetTime.setUTCHours(24, 0, 0, 0); // تنظیم به نیمه شب گرینویچ
    return targetTime.getTime() - now.getTime();
  };

  return (
    <div className="col-12 mb-3 dashboard-arbibot d-flex flex-row flex-wrap">
      <h6 className="col-12">Your ArBot Level</h6>
      <div className="col-12">
        <small>
          <i>You Can't Active ArBot in this xG Plan Stage.</i>
        </small>
      </div>

      <div className="col-12 ">
        <div className="container">
          <div className="row">
            <div className="col-12 arbot-stage d-flex flex-wrap justify-content-between align-items-center ">
              <div className="col-4">
                <img
                  src={stkArBot}
                  className="arbot-stage-sticker"
                  alt="ArBot Sticker"
                />
              </div>
              <div className="col-8">
                <h6>Your ArBot</h6>
                <div>
                  <table className="table table-borderless table-active-arbot">
                    <tbody>
                      <tr>
                        <td>Balance </td>
                        <td>15 USDT</td>
                      </tr>
                      <tr>
                        <td>Level </td>
                        <td>1</td>
                      </tr>
                      <tr>
                        <td>P.P.D. (%) </td>
                        <td>0.85%</td>
                      </tr>
                      <tr>
                        <td>P.P.D. ($) </td>
                        <td>0.127 USDT</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="table-responsive text-center col-12 mt-2">
        <table className="table table-borderless table-active-stage">
          <thead>
            <tr>
              <th>Resource</th>
              <th>Daily Profit</th>
              <th>Claim</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>/Direct</td>
              <td>0.127 USDT</td>
              <td>
                <Link to="/my-twa/xg-plan">
                  <div className="btn btn-warning btn-sm">
                    <img
                      src={icoArrowUp}
                      className="upgrade-stage-icon"
                      alt="^"
                    />
                  </div>
                </Link>
              </td>
            </tr>
            <tr>
              <td>/xG Team</td>
              <td>3.5 USDT</td>
              <td>
                <Link to="/my-twa/xg-plan">
                  <div className="btn btn-warning btn-sm">
                    <img
                      src={icoArrowUp}
                      className="upgrade-stage-icon"
                      alt="^"
                    />
                  </div>
                </Link>
              </td>
            </tr>
            <tr>
              <td colSpan="3">
                <Countdown
                  date={Date.now() + calculateTimeRemaining()}
                  renderer={({ hours, minutes, seconds, completed }) => {
                    if (completed) {
                      return <Completionist />;
                    } else {
                      return (
                        <>
                          <span>Remaining time of day </span>
                          <strong
                            style={{ fontSize: ".8rem" }}
                            className="badge  bg-dark">
                            {hours}:{minutes}:{seconds}
                          </strong>
                        </>
                      );
                    }
                  }}
                />
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default DashboardArbot;
